@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
	--base-1: #EEE841;
	--green: #53C72B
  }
  
html
{
	padding:0px;
	margin:0px;
}
body
{
	padding:0px;
	margin:0px;
	font-family: 'Roboto', sans-serif !important; 
	background-color: #E8EAEF !important;
}
a
{
	color: #000 !important;
}
.profile-section
{
	display: flex;
	flex-direction: column;
	align-items: center;
}
.profile-section .profile-username
{
	font-size: 20px;
	font-weight: 400;
}
.profile-section .profile-name
{
	font-size: 50px;
	font-weight: bold;
}
.profile-section .profile-image img
{
	width:200px;
	height:200px;
	border-radius: 50%;
}
.profile-section .profile-image 
{
	display: flex;
	margin-bottom: 20px;
	flex-direction: row;
}
.profile-section .profile-image img
{
	z-index:1 !important
}
.profile-section .profile-bg
{
	width:200px !important;
	height:200px !important;
	background-color: #EEE841;
	background-color: var(--base-1);
	border-radius: 50%;
	z-index: 0;
	margin-left: -150px;
}
.nav 
{
	padding: 20px 0px;
}
.nav .nav-logo
{
	font-weight: bolder;
	font-size: 20px;
	border: 2px solid #EEE841;
	border: 2px solid var(--base-1);
	height:70px;
	width:70px;
	border-radius: 50%;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}
.nav-right 
{
	display: flex;
	justify-content:flex-end;
	margin-top: 20px;
	align-items: center;
}
.nav-right .nav-btn
{
	background-color: #EEE841;
	background-color: var(--base-1);
	padding: 10px 20px;
	font-weight: 600;
	letter-spacing: 2px;
}

.profile-hire-status::before
{
	background-color:#53C72B;
	background-color:var(--green);
	content:'';
	display: inline-block;
	height:10px;
	width:10px;
	border-radius: 50px;
	margin-right: 10px;
}
.profile-location
{
	display: flex;
	margin-bottom: 10px;
}
.profile-location .profile-location-icon
{
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 20px;
	background-color: black;
	margin-right: 4px;
	color: #EEE841;
	color: var(--base-1);
}
.profile-location .profile-location-text
{
	background-color: black;
	color: #E8EAEF;
	padding: 10px 20px;
	font-weight:bold;
	display: flex;
	align-items: center;
	justify-content: center;
}
.about-section
{
	padding-left:50px;
}
.bl
{
	border-left: 1px solid grey;
}
.about-section .about-profile h1
{
	font-weight: bold;
	font-size: 32px;
}
.about-section .about-profile h2
{
	font-weight: bold;
	font-size: 18px;
}
.about-profile 
{
	margin-bottom: 40px;
}
.about-profile span
{
	background-color: #EEE841;
	background-color: var(--base-1);
	margin-top:10px;
	margin-right:10px;
	padding: 10px;
	display: inline-block;
}
.project-item
{
	background-color: #fff;
	box-shadow: 10;
	-webkit-filter: drop-shadow(0px 9px 7px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(0px 9px 7px rgba(0, 0, 0, 0.25));
	padding: 20px;
	margin-bottom: 20px;
}
.project-item .project-title
{
	font-weight: bold;
	font-size: 20px;
	margin-bottom: 10px;
}
.project-item .project-link
{
	opacity: 0.8;
	margin-bottom: 10px;
}
.project-item .project-desc
{
	margin-bottom: 10px;
	word-wrap: break-word;
} 
.project-item .project-tags span
{
	display: inline-block;
	background-color: #E8EAEF;
	font-weight:bold;
	padding: 10px 15px;
	font-size:14px;
	border-radius: 20px;
}
span.ico
{
	background-color: transparent;
	font-size: 30px;
	opacity: 0.5;
}
.skills
{
	list-style: none;
	padding: 0px;
	display: flex;
	flex-wrap:wrap;
}
.skills li
{
	margin:10px;
	border:2px solid #000;
	border-radius: 50%;
	height:100px !important;
	width:100px !important;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: bold;
	font-size: 14px;
}
.skills li img
{
	height: 80px;
	position: absolute;
	opacity: 0.09;
	border-radius: 50px;
}
.filter-btn
{
	list-style: none;
	padding: 0px;
	display: flex;
	flex-wrap:wrap;
}
.filter-btn li
{
	border:2px solid grey;
	padding: 10px 20px;
	border-radius: 20px;
	font-size: 12px;
}
.filter-text
{
	border:0px solid transparent !important;
	display: flex;
	align-items: center;
	font-size:18px !important;
	color: grey !important;
}
.filter-btn .btn
{
	background-color:transparent ;
	border:0px solid transparent
}
.profile-not-found
{
	height:100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size:100px;
	font-weight: bolder;
	opacity: 0.5;
	flex-direction: column;
}
.about-profile h1 span
{
	font-size:14px;
	border-radius: 50%;
	position: absolute;
	margin-top: -10px;
	margin-left: -10px;
	z-index: -1;
	height:40px;
	width:40px;
	text-align: center;
	line-height: 20px;
}

.footer-ico::after
{
	content: url(data:image/png;base64,);
	display: inline;
	right:20px;
	opacity: 0.5;
	margin-left: auto;
}
.footer-ico
{
	list-style: none;
	padding: 0px !important;
	display:flex;
	margin-top:20px;
	margin-bottom: 0px;
} 
.footer-ico li
{
	margin:0px 10px;
	font-weight:bold
}
.switch {
	display: flex;
	height: 36px;
	position: relative;
	width: 70px;
	margin-right: 20px;
  }
  
  .switch input {
	display:none;
  }
  
  .slider {
	background-color: #FFF;
	border:1px solid #000;
	bottom: 0;
	cursor: pointer;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	transition: .4s;
  }
  
  .slider:before {
	background-color: #333;
	border:1px solid #000;
	bottom: 3px;
	content: "";
	height: 26px;
	left: 4px;
	position: absolute;
	transition: .4s;
	width: 26px;
	z-index:421;
  }
  
  input:checked + .slider {
	background-color: #101211;
  }
  
  input:checked + .slider:before {
	transform: translateX(34px);
	border-color:#CFCFCF;
	background-color:#EFEFEF;
  }
  
  .slider.round {
	border-radius: 34px;
  }
  
  .slider.round:before {
	border-radius: 50%;
  }
  
  .toggle-moon, .toggle-sun {
	width:50%;
	text-align:center;
	padding:0.25em;
	position:relative;
	z-index:420;
  }
  
@media all and (max-width: 480px) { 
	.project-link
	{
		display: none;
	}
	.about-section
	{
		margin: 30px 0px;
		padding: 0px !important;
	}
	.bl
	{
		border:0px solid transparent
	}
	.bl-sm
	{
		border-left: 1px solid grey;
	}
	.abouts-stats
	{
		text-align: center;
	}
	.skills li
	{
		width:80px !important;
		height:80px !important
	}
	.filter-text
	{
		display:block;
		width:100%;
	}
	.filter-btn .btn 
	{
		padding: 5px 10px !important;
	}
	.nav-btn
	{
		margin-top: 20px;
		border-radius: 50%;
		height:50px;
		width:50px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.nav-btn span
	{
		display: none;
	}
}
/* darkmode */
.darkmode
{
	background: #121212 !important;
	
}
.darkmode , .darkmode a
{
	color: #B3B3B3 !important;
}
.darkmode .filter-btn li
{
	color:#B3B3B3;
	border-color: #282828;
}
.darkmode .filter-btn .btn:hover
{
	background: transparent !important;
}
.darkmode .project-item
{
	background: #282828 !important;
}
.darkmode .project-tags 
{
	color: #282828 !important;
	opacity: 0.2;
}
.darkmode .profile-bg
{
	background-color: #282828;
}
.darkmode .nav-btn ,.darkmode .skills li
{
	background-color: #282828;
}

.darkmode .profile-location-icon
{
	color: #B3B3B3;
}
.darkmode .footer-ico::after
{
	-webkit-filter: brightness(60);
	        filter: brightness(60);
}
.darkmode .bl
{
	border-color: #B3B3B3;
}
